<template>
     <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  .
            &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
            <ul class="cart__orders">
            <li  class="cart__order" v-for="cartProduct in orderInfo.basket.items"
            :key="cartProduct.id">
              <h3>{{ cartProduct.product.title }}</h3>
              <b>{{ cartProduct.product.price | numberFormat }} </b>
              <span>: {{ cartProduct.product.id }}</span>
              <b >{{ cartProduct.quantity }} </b>
            </li>
          </ul>
            <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{ amountOrder }}</b>
              
            <b>{{ $store.state.orderInfo.totalPrice + 500 | numberFormat}}
              </b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

import numberFormat from '@/helpers/numberformat';

export default {
  filters: { numberFormat },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo;
    },
    amountOrder() {
      return this.$store.state.orderInfo.basket.items.reduce((acc, item) => item.quantity + acc, 0);
    },
  },
};
</script>
