<template>
  <main class="content container">
    <preloaderRouter style= "bottom: 50%; left: 50%" />
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        3 
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent='order'>
        <div class="cart__field">
          <div class="cart__data">
             <BaseFormText v-model="formData.name"
             title='' type='text' placeholder="   " :error="formError.name"/>

            <BaseFormText v-model="formData.address"
             title=' ' type='text'
             placeholder="  " :error="formError.address"/>

            <BaseFormText v-model="formData.phone"
             title='' type='tel' placeholder="  " :error="formError.phone"/>

            <BaseFormText v-model="formData.email"
             title='Email'  type="email" placeholder="  Email" :error="formError.email"/>

             <BaseFormTextarea v-model="formData.comment"
             title='  '
             placeholder=" " :error="formError.comment"/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only"
                   type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <OrderCart />

          <button class="cart__button button button--primery" type="submit">
             
          </button>
          <Preloader v-show="orderLoading"
          style= " width: 300px; height: 300px; top: 25%; right: 10%;"  />
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>

</template>
<script>

import axios from 'axios';
import { API_BASE_URL } from '../config';
import BaseFormText from '../components/BaseFormText.vue';
import BaseFormTextarea from '../components/BaseFormTextarea.vue';
import OrderCart from '../components/OrderCart.vue';
import Preloader from '../components/Preloader.vue';
import PreloaderRouter from '../components/PreloaderRouter.vue';

export default {
  components: {
    BaseFormText,
    BaseFormTextarea,
    OrderCart,
    Preloader,
    PreloaderRouter,
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderLoading: false,
    };
  },
  methods: {
    order() {
      this.orderLoading = true;
      clearTimeout(this.loadOrderTimer);
      this.formError = {};
      this.formErrorMessage = '';
      this.loadOrderTimer = setTimeout(() => {
        axios
          .post(`${API_BASE_URL}/api/orders`, {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then((response) => {
            this.$store.commit('resertCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => { this.orderLoading = false; });
      }, 500);
    },
  },
};
</script>
